.page {
  margin: 0;
  font-family: Inter, sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

.page:has(.menu:target) {
  overflow: hidden;
}

.page__section {
  margin-bottom: 110px;
  display: grid;
}

@media (min-width: 1024px) {
  .page__section {
    margin-bottom: 170px;
  }
}

@media (min-width: 744px) {
  .page__section {
    margin-bottom: 140px;
  }
}

.grid {
  width: 100%;
  display: block;
}

@media (min-width: 744px) {
  .grid {
    column-gap: 24px;
    display: grid;
  }
}

@media (min-width: 1024px) {
  .grid {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 30px;
    display: grid;
  }
}

.grid--desktop {
